@import "https://fonts.googleapis.com/css2?family=Martian+Mono:wght@500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --index: calc(1vw + 1vh);
  --color-dark: #262b2f;
  --color-primary: #94d2bd;
  --color-primary2: #ec3b85;
  --color-secondary: #ee9b00;
}

body {
  font-family: Martian Mono, monospace;
}

.main-container {
  position: relative;
}

.clock-container {
  z-index: 2;
  background: url("dark_noise.6af583f7.jpg") 0 0 / 50px 50px;
  border-radius: 36px;
  padding: 1rem;
  box-shadow: 0 24px 48px #0000001c, 0 3px 24px #00000054;
}

.clock-container div:nth-child(2n) {
  transition: all .33s ease-in-out;
}

.clock-container.timer div:nth-child(2n) {
  color: var(--color-primary);
}

.clock-container.countdown div:nth-child(2n) {
  color: var(--color-primary2);
}

.clock-digits {
  font-size: calc(var(--index) * 5);
}

.mode-container {
  height: 100%;
  width: 66%;
  background: url("dark_noise.6af583f7.jpg") 0 0 / 75px 75px;
  border-radius: 36px;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -100%);
  box-shadow: 0 24px 48px #0000001c, 0 3px 24px #00000054;
}

.mode-container .progress-bar {
  width: 100%;
  height: 3%;
  z-index: 1;
  border-radius: 0 5px 5px 0;
  transition: width 30ms ease-in-out;
  position: relative;
}

.mode-container .progress-bar.countdown:after {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, var(--color-primary) 10% 50%, var(--color-primary2));
}

.mode-container .progress-bar.timer {
  background-color: var(--color-primary);
}

.mode-container .progress-bar.countdown {
  background-color: var(--color-primary2);
}

.mode-container.timer .mode-switcher {
  height: 50%;
}

.mode-container.countdown .mode-switcher {
  height: 22%;
}

.mode-switcher {
  width: 100%;
  z-index: 0;
  justify-content: center;
  align-items: center;
  transition: height .33s ease-in-out;
  display: flex;
}

.mode1, .mode2 {
  width: 50%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mode-switcher input[type="radio"] {
  opacity: 0;
  width: 0;
  position: fixed;
  pointer-events: none !important;
}

.mode-switcher label {
  cursor: pointer;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: calc(var(--index) * .8);
  background: url("dark_noise.6af583f7.jpg") 0 0 / 75px 75px;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.mode-switcher input[type="radio"]:checked + label {
  color: var(--color-dark);
}

.mode-switcher input[type="radio"]#timer-radio:checked + label {
  background: var(--color-primary);
}

.mode-switcher input[type="radio"]#countdown-radio:checked + label {
  background: var(--color-primary2);
}

.countdown-form {
  padding: 1%;
}

.countdown-form input {
  width: 20%;
  text-align: center;
  color: var(--color-dark);
  background-color: #ffffffe6;
  border: none;
  border-radius: 5px;
  margin: 0 5px;
  padding: 1%;
}

.countdown-form input:focus {
  outline: none;
}

.countdown-form input::placeholder {
  text-align: center;
}

.countdown-form input::-webkit-outer-spin-button, .countdown-form input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.countdown-form input {
  -moz-appearance: textfield;
}

.timer-buttons-container {
  height: 100%;
  width: 66%;
  z-index: 1;
  background: url("dark_noise.6af583f7.jpg") 0 0 / 75px 75px;
  border-radius: 36px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
  box-shadow: 0 24px 48px #0000001c, 0 3px 24px #00000054;
}

.timer-buttons-container.show {
  display: block;
}

.countdown-buttons-container {
  height: 100%;
  width: 66%;
  z-index: 1;
  background: url("dark_noise.6af583f7.jpg") 0 0 / 75px 75px;
  border-radius: 36px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
  box-shadow: 0 24px 48px #0000001c, 0 3px 24px #00000054;
}

.countdown-buttons-container.show {
  display: block;
}

.buttons {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.control-button {
  font-size: calc(var(--index) * 3.3);
  cursor: pointer;
  color: var(--color-secondary);
  margin-top: 17%;
}

.timer-buttons-container .control-button:hover {
  color: var(--color-primary);
}

.countdown-buttons-container .control-button:hover {
  color: var(--color-primary2);
}

.control-button:active {
  transform: scale(.9);
}

.timer-buttons-container .control-button.active {
  color: var(--color-primary);
}

.countdown-buttons-container .control-button.active {
  color: var(--color-primary2);
}

.saved-timers-container {
  min-width: 66%;
  z-index: 1;
  position: fixed;
  bottom: 8%;
}

.saved-timers-container button {
  font-size: calc(var(--index) * 1);
  color: var(--color-secondary);
}

.saved-timers-container button:hover {
  color: var(--color-primary);
}

.saved-timers-container button:active {
  transform: scale(.98);
}

.saved-timers-container button.hidden {
  opacity: 0;
}

.saved-timers div:nth-child(2n) {
  color: var(--color-primary);
}

.saved-timer {
  font-size: calc(var(--index) * .6);
}

@media (max-width: 768px) {
  .clock-digits {
    font-size: 70px;
  }
}

@media (max-width: 576px) {
  .clock-digits {
    font-size: 48px;
  }

  .clock-units {
    font-size: 10px;
  }

  .countdown-form input {
    padding: 0;
  }
}

